@import "../../global.scss";
.testimonials{
    background-color:white;
    display: flex;
    align-items: center;
    flex-direction: column;
    h1{
        font-size: 50px;
        overflow: hidden;
        @include mobile{
            font-size: 30px;
        }
    }
    .container{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile{
           flex-direction: column;
        }
        .card{
            width: 250px;
            height: 50%;
            border-radius: 10px;
            box-shadow: 0px 0px 15px -8px black;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 20px;
            transition: all 1s ease;
            @include mobile{
                height: 180px;
                margin: 10px 0;
                width: 70%;
            }
            &.featured{
                width: 300px;
                height: 70%;
                margin: 0 30px;
                @include mobile{
                    width: 70%;
                    height: 180px;
                    margin: 1px;
                }
               
            }
            &:hover{
                transform: scale(1.1);
            }
            .top{
                display: flex;
                align-items: center;
                justify-content: center;
                overflow: hidden;
                img{
                    &.left,&.right{
                        height: 25px;
                    }
                    &.user{
                        height: 60px;
                        width: 60px;
                        border-radius: 50%;
                        object-fit: cover;
                        margin: 0 40px;
                        @include mobile{
                            width: 30px;
                            height: 30px;
                        }
                    }
                }
            }
            .center{
                padding: 10px;
                background-color: rgb(245, 229, 232);
                border-radius: 10px;
                overflow: hidden;
                @include mobile{
                    font-size: 12px;
                    padding: 5px;
                }
            }
            .bottom{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                overflow: hidden;
                h3{
                    margin-bottom: 5px;
                    @include mobile{
                        font-size: 14px;
                    }
                }
                h4{
                    color: gray;
                    @include mobile{
                        font-size: 14px;
                    }
                }
            }
        }
    }
}