@import "../../global.scss";
.topbar{
    width: 100%;
    height: 70px;
    background-color: white;
    position: fixed;
    top: 0;
    z-index: 3;
    color: $mainColor;
    transition: all 1s ease;
    .wrapper{
        padding:10px 30px; //first value is for top and bottom and second value is for left and right 
        display: flex;
        align-items: center;
        justify-content: space-between;
        .left{
            display: flex;
            align-items: center;
            .logo{
                font-size: 40px;
                font-weight: bold;
                text-decoration: none;
                color:inherit;
                margin-right: 40px;
            }
            .itemContainer{
                display:flex;
                align-items: center;
               margin-left: 30px;
               @include mobile{
                display: none;
            }
               .icon{
                font-size: 18px;
                margin-left: 5px;
               }
               span{
                   font-size: 15px;
                   font-weight: 500;
               }
            }
        }
        .right{
            
            .hamburgermenu{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                span{
                    width: 100%;
                    height:3px;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }
    &.active{
        background-color: $mainColor;
        color:white;
        .hamburgermenu{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}