@import "../../global.scss";
.intro{
    background-color: white;
    display:flex;
    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    
    .left{
        flex: 0.5;
        overflow: hidden;
        .imageContainer{
            overflow: hidden;
            width: 700px;
            height: 700px;
            background-color: crimson;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: flex-start;
            float: right;
            img{
                width: 90%;
                margin-top: 90px;
               @include mobile{
                   height: 100%;
                   margin-top: 0;
               }
            }
        }

    }
    .right{
        flex: 0.5;
        position: relative;
        .wrapper{
           width: 100%;
           height: 100%;
           display: flex;
           flex-direction: column;
           justify-content: center;
           padding-left: 50px;
           @include mobile{
               padding-left: 0;
               align-items: center;

           }
          h1{
            font-size: 60px;
            margin:10px 0px;
            @include mobile{
                font-size: 40px;
            }
          }
          h2{
            font-style: 35px;
          }
          h3{
            font-style: 30px;
            @include mobile{
                font-size: 20px;
            }
            span{
                font-size: inherit;
                color:crimson;
            }
            .ityped-cursor{
                animation:blink 1s infinite;
            }
            @keyframes blink {
                50%{
                    opacity: 1;
                }
                100%{
                    opacity: 0;
                }
                
            }
          }
        }
        a{
            position:absolute;
            bottom: 10px;
            right:40%;
            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }
            @keyframes arrowBlink{
                100%{
                    opacity: 0;
                }
            }
        }
    }
}